
import Search from 'vux/src/components/search/index.vue'

import { mapState, mapMutations } from 'vuex'

export default {
  components: {
    Search
  },
  data () {
    return {
      searchMsg: ['找电影、影人、影院、演出、赛事', '请输入艺人、演出、场馆...', '找赛事，找场馆'],
      value: ''
    }
  },
  computed: {
    ...mapState([
      'viewIndex'
    ]),
    placeholder () {
      return this.searchMsg[this.viewIndex];
    }
  },
  methods: {
    ...mapMutations([
      'initViewIndex'
    ])
  }
}



